tr.no-items {
  text-align: center;
}

.wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api) {
  display: flex !important;
  padding-left: 5px !important;
  padding-right: 40px !important;
  align-items: flex-start !important;
  flex-direction: column;
  justify-content: left !important;
  position: relative !important;
  min-height: 43px;
}
.wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api) p {
  line-height: 30px;
  margin-bottom: 10px !important;
}
.wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api) p:last-child, .wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api) p:last-of-type {
  margin-bottom: 0 !important;
}
.wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api) button {
  margin-left: auto !important;
  margin-bottom: 0 !important;
  position: absolute !important;
  top: 5px !important;
  right: 5px !important;
  border: 1px solid var(--color-darkGrey) !important;
  background-color: transparent !important;
  padding: 0 4px !important;
}
.wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api) button:before {
  width: 20px;
  height: 20px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("../icons/icon_close.svg") !important;
  content: "";
}
.wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api).notice-success {
  border-left-color: #4BB543;
}
.wrap.cbx-chota div.notice:not(#lost-connection-notice):not(#local-storage-notice):not(#notice-corrupt-rest-api):notice-error {
  border-left-color: #ff4540;
}